:root{
	font-size: 10px;
}
@font-face{
	font-family: MontserratBold;
	src: url(../fonts/MontserratBold.ttf);
}
@font-face{
	font-family: MontserratRegular;
	src: url(../fonts/MontserratRegular.ttf);
}
@font-face{
	font-family: MontserratSemibold;
	src: url(../fonts/MontserratSemiBold.ttf);
}

$white-color: #fff;
$contact-color: #191e60;
$blue-color: #3a4ba5;
$lightblue-color: #071739;
$sidebar-bg: #f2f2f2;
$button-padding: 15px 34px;
$button-fz: 1.67rem;
$subtext: 1.45rem;
$contact-fz: 2.5rem;
$semibold: MontserratSemibold, sans-serif;
$bold: MontserratBold, sans-serif;

body{
	margin: 0;
	overflow-x: hidden;
	font-family: MontserratRegular, sans-serif;
}
*,
*:before,
*:after{
	box-sizing: border-box;
}
a{
	text-decoration: none;
}
ul{
	padding: 0;
	margin: 0;
	list-style-type: none;
}
.button{
	outline: none;
	border: none;
	cursor: pointer;
	background: $white-color;
	font-size: $button-fz;
	padding: $button-padding;
	font-family: $bold;
	border-radius: 25px;
	transition: .2s linear;
}
.button:hover{
	opacity: .6;
}
.button--blue{
	background: $blue-color;
	color: $white-color;
}
.button--white{
	border: 2px solid $blue-color;
	color: $blue-color;
}

/* Header */
/* Header top */

.header__top--inner{
	display: flex;
	align-items: center;
}
.header__menu--inner{
	display: flex;
	flex-direction: column;
	width: 100%;
	border-left: 1px solid #eaeaea;
}
.header__menu--top{
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 30px 50px;
	border-bottom: 1px solid #eaeaea;
}
.header__menu--bottom{
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0px 50px;
}
.header__logo{
	padding: 0px 25px;
}
.header__title{
	font-size: 2.1rem;
	color: $lightblue-color;
	font-family: $semibold;
}
.header__span--blue{
	color: $blue-color;
	font-family: $bold;
}

/* Header bottom */

.header__menu--link{
	font-size: $button-fz;
	color: $lightblue-color;
	margin: 16px 0px;
	font-family: $semibold;
	transition: .2s linear;
}
.header__menu--link:hover{
	color: $blue-color;
}
.header__menu--link:first-child{
	padding-left: 0;
}
.header__menu--link:nth-child(6){
	margin-right: 10px;
}
.header__menu--glasses{
	margin: 0;
	padding: 16px 40px 16px 100px;
	background: $blue-color;
	color: $white-color;
	position: relative;
	transition: .2s linear;
}
.glasses{
	font-size: $contact-fz;
	position: absolute;
	top: 50%;
	left: 40px;
	transform: translateY(-50%);
}
.header__menu--glasses:hover{
	color: #000;
	opacity: .9;
}

/* Header slider left */

.header__slider--inner{
	display: flex;
	align-items: center;
}
.header__slider--left{
	width: 44%;
	padding-top: 50px;
	padding-bottom: 50px;
	padding-right: 50px;
	padding-left: calc(100vw - 1700px);
	background: $lightblue-color;
}
.header__slider--right{
	width: 56%;
	height: 421px;
	overflow: hidden;
}
.slider__item--title{
	font-size: $contact-fz;
	color: $white-color;
	font-family: $bold;
}
.slider__item--text{
	font-size: $button-fz;
	color: $white-color;
	font-family: $semibold;
}
.slick__button{
	border: none;
	padding: 15px 74px;
	margin-top: 60px;
}
li.slick-active{
    width: 1.3rem;
    height: 1.3rem;
    background: $blue-color;
    border-radius: 50%;
}
li.slick-active:hover{
	background: $blue-color;
}
li{
    display: inline-block;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 50%;
    margin-right: 17px;
    cursor: pointer;
    background: $white-color;
    transition: .2s linear;
}
li:hover{
    background: #838b9c;
}
.comment__choise li button{
	opacity: 0;
	cursor: pointer;
}
.comment__choise{
	position: relative;
	top: 93px;
	left: 350px;
}

/* Header slider right */

.slider__arrow{
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background: $blue-color;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	position: absolute;
	z-index: 1000;
	bottom: 50px;
	transition: .2s linear;
}
.slider__arrow--right, .footer__arrow--right{
	left: 120px;
}
.slider__arrow--left, .footer__arrow--left{
	left: 50px;
}
.slider__arrow:hover{
	background: $white-color;
}
.slider__arrow:hover .arrow{
	color: $blue-color;
}
.arrow{
	color: $white-color;
	font-size: 1.5rem;
}
.slider__right--img{
	position: relative;
	top: -307px;
}
.slider__right{
	width: 100%;
	height: 100%;
}

/* Strukture */

.strukture{
	display: flex;
	justify-content: space-between;
}
.left__sidebar{
	width: 360px;
	padding: 50px 0;
	box-shadow: 0px 0px 30px 0px rgba(3, 3, 57, 0.2);
}
.left__sidebar--column{
	border-bottom: 1px solid $sidebar-bg;
}
.left__sidebar--column:last-child{
	border-bottom: 0;
}
.column__item, .column__item-2, .column__item--vlo, .column__item--vlo--vlo{
	cursor: pointer;
	padding: 25px 35px 25px 50px;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	align-items: center;
	transition: .2s linear;
}
.column__item--vlo{
	padding-left: 75px;
	display: none;
}
.column__item--vlo--vlo{
	padding: 10px 10px 10px 75px;
	display: none;
}
.column__item--vlos{
	border-left: 1px solid $sidebar-bg;
	padding-left: 25px;
}
.column__item--text, .fa-plus, .fa-minus, .column__item-2, .column__item--text--vlo{
	color: $lightblue-color;
	font-size: $button-fz;
	font-family: $semibold;
}
.column__item--text--vlo{
	font-size: $subtext;
	display: inline-block;
	margin-top: 10px;
}
.column__item:hover, .column__item-2:hover, .column__item--vlo:hover{
	background: $sidebar-bg;
}
.column__item--text{
	margin-right: 15px;
}
.blue{
	color: $blue-color;
}
.active{
	background: $blue-color;
}
.active .column__item--text{
	color: $white-color;
}
.active:hover{
	background: $blue-color;
}
.active:hover .column__item--text{
	color: $white-color;
}
.fa-minus{
	color: $white-color;
}
.active .fa-plus{
	display: none;
}
.column__item.active .fa-minus, .column__item--vlo.active .fa-minus{
	display: inline-block;
}
.column__item .fa-minus, .column__item--vlo .fa-minus{
	display: none;
}
.column__item--vlo.active__vlo .fa-minus{
	display: inline-block;
}
.column__item--vlo.active__vlo .fa-plus{
	display: none;
}
.column__item--vlo .fa-minus{
	color: #000;
}

/* News */

/* Right sidebar */

.right__sidebar{
	position: relative;
	padding: 150px 50px;
	max-width: 380px;
	background: $sidebar-bg;
} 
.right__sidebar--item{
	margin-top: 50px;
}
.right__arrows{
	font-size: 2.1rem;
	color: $white-color;
}
.right__arrow{
	cursor: pointer;
	z-index: 555;
	position: absolute;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background: $blue-color;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: .3s linear;
}
.right__arrow:hover{
	opacity: .6;
}
.right__slider--arrow--up{
	top: -50px;
	right: 50%;
	transform: translateX(40%);
}
.right__slider--arrow--down{
	bottom: -100px;
	right: 50%;
	transform: translateX(40%);
}









/* Footer Slider */

.footer__slider{
	padding: 50px 205px;
	background: #f2f2f2;
}
.footer__slider--title{
	font-size: 3.33rem;
	color: $lightblue-color;
	font-family: $semibold;
	text-align: center;
}
.footer__slider--inner{
	margin-top: 50px;
}
.slider__link{
	margin-right: 50px;
}
.footer__arrow--right{
	left: 100%;
    top: 110px;
}
.footer__arrow--left{
	left: -6%;
    top: 110px;
}
.footer__contact--inner{
	display: flex;
	padding: 50px 0;
	border-bottom: 1px solid #e6e7eb;
}
.footer__contact--inner:last-child{
	border-bottom: 0;
}
.footer__contact{
	padding: 50px 180px;
}
.contact__title{
	font-size: $contact-fz;
	color: $contact-color;
	font-family: $bold;
}
.contact__text{
	color: $lightblue-color;
}
.contact__link{
	transition: .2s linear;
	color: $blue-color;
}
.contact__link:hover{
	opacity: .6;
}
.contact__link, .contact__text{
	margin-top: 25px;
	font-size: $button-fz;
	font-family: $semibold;
}
.address, .email, .phone{
	width: 25%;
	margin-right: 50px;
}
.address{
	width: 35%;
}
.hours{
	width: 40%;
}
.graphic{
	width: 28%;
}
.footer__menu{
	margin-top: 50px;
	padding: 50px 170px 35px 170px;
	background: $lightblue-color;
}
.footer__menu--item{
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	width: 20%;
}
.footer__menu--item:last-child{
	width: 40%;
	justify-content: space-between;
	align-items: flex-end;
}
.footer__menu--inner{
	display: flex;
}
.footer__menu--title{
	font-size: $contact-fz;
	color: $white-color;
	font-family: $bold;
}
.footer__menu--links{
	margin-top: 50px;
}
.footer__menu--link{
	color: $white-color;
	font-size: $subtext;
	font-family: $semibold;
	margin-top: 10px;
	display: inline-block;
}
.footer__button{
	border: none;
}
.fa-long-arrow-alt-up{
	margin-left: 7px;
}
.footer__grey{
	font-size: $subtext;
	color: #757575;
	font-family: $semibold;
}
.copyright{
	font-family: $semibold;
	color: $white-color;
	font-size: $subtext;;
}
.footer__light{
	font-family: MontsseratRegular;
}
.footer__menu--adress, .menu__link{
	color: $white-color;
	font-size: $subtext;
	margin-top: 16px;
	display: inline-block;
}
.footer__menu--adress{
	margin-top: 60px;
}